import React, { useEffect } from "react";
import { FiChevronUp } from "react-icons/fi";
import ScrollUpButton from "react-scroll-up-button";
import { IoIosArrowBack } from "react-icons/io";
import { IoIosArrowForward } from "react-icons/io";
import { scroller } from "react-scroll";
import { useHistory, Link as Link1 } from "react-router-dom";
import { motion } from "framer-motion";
import Zoom from "react-medium-image-zoom";
import "react-medium-image-zoom/dist/styles.css";
import Contact from "../Contact";
import Overlay from "../Overlay";

import vg1 from "../../photos/vg1.jpg";
import vg2 from "../../photos/vg2.jpg";
import vg3 from "../../photos/vg3.jpg";
import vg4 from "../../photos/vg4.jpg";
import vg5 from "../../photos/vg5.jpg";
import vg6 from "../../photos/vg6.jpg";
import vg7 from "../../photos/vg7.jpg";

const images = [vg1, vg3, vg2, vg7, vg4, vg5, vg6].map((url) => (
  <div className="project-site__image-section">
    <Zoom
      alt="that wanaka tree"
      overlayBgColorStart={"#191919"}
      overlayBgColorEnd={"#191919"}
      closeText="3213123"
      openText="3213123"
      zoomMargin={40}
    >
      <img src={url} className="project-site__image" />
    </Zoom>
  </div>
));

export default function OnlineLibrary() {
  let history = useHistory();
  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);
  useEffect(() => {
    setTimeout(() => {}, 1000);
    return () => {};
  });

  return (
    <>
      <motion.div className="project-site-wrapper">
        <div className="project-site__name">
          <h1 className="project-site__title">Velluto Giorno</h1>
          <div className="project-site__date">Luty 2020 - Kwiecień 2020</div>
        </div>

        <div className="project-site">
          <div className="project-site__info">
            <p className="project-site__main-title">O projekcie:</p>
            <p className="project-site__text">
              Sklep internetowy dla lokalnej firmy odzieżowej. Platforma została
              zbudowana całkowicie samodzielnie.
            </p>
            <p className="project-site__text bold">
              Aplikacja pozwala użytkownikowi na:
            </p>

            <ul className="project-site__ulist">
              <li className="project-site__ulist-item">
                przeglądanie dostępnych przedmiotów
              </li>
              <li className="project-site__ulist-item">
                filtrowanie przedmiotów po kategorii, płci, cenie, rozmiarze,
                kolorze, typie produktu
              </li>
              <li className="project-site__ulist-item">
                obsługę koszyka (dodawanie/usuwanie przedmiotów)
              </li>
              <li className="project-site__ulist-item">składanie zamówień</li>
            </ul>
            <p className="project-site__text bold">Panel admina pozwala na:</p>
            <ul className="project-site__ulist">
              <li className="project-site__ulist-item">
                dodawanie nowych przedmiotów z podglądem widoku użytkownika
              </li>
              <li className="project-site__ulist-item">
                tabelę wszystkich dostępnych przedmiotów, ułatwiająca
                zarządzanie nimi
              </li>
              <li className="project-site__ulist-item">
                tabele z obecnymi zamówieniami
              </li>
              <li className="project-site__ulist-item">
                zarządzanie przecenionymi, polecanymi produktami
              </li>
            </ul>
            <br />
            <br />
            <br />
            <div className="project-site__technologies">
              <ul className="project-site__list project-site__list--pages">
                <a
                  href="http://vellutogiorno.pl/"
                  className="project-site__list-item--more"
                  target="_blank"
                >
                  Zobacz stronę
                </a>
              </ul>
            </div>
          </div>

          <div className="project-site__info project-site__info--border">
            <p className="project-site__main-title">Podsumowanie techniczne:</p>
            <p className="project-site__text">
              Aplikacja wykorzystująca stak technologiczny MERN.
            </p>
            <p className="project-site__subtitle project-site__subtitle--big">
              Aplikacja Front-end
            </p>

            <ul className="project-site__ulist">
              <li className="project-site__ulist-item">
                stworzona przy użyciu React'a oraz czystego CSS
              </li>
              <li className="project-site__ulist-item">
                zawiera walidację formularzy
              </li>
              <li className="project-site__ulist-item">
                wysyła zapytania do API
              </li>
            </ul>

            <p className="project-site__subtitle project-site__subtitle--big">
              Serwer Back-end
            </p>

            <ul className="project-site__ulist">
              <li className="project-site__ulist-item">
                wykonany przy użyciu NodeJS z bazą danych MongoDB
              </li>
              <li className="project-site__ulist-item">
                obsługuje wszystkie zapytania
              </li>
              <li className="project-site__ulist-item">
                serwuje aplikację front-endową
              </li>
              <li className="project-site__ulist-item">
                posiłkuje się biblioteką Multer, do zapisu obrazów na serwer
              </li>
            </ul>
            <p className="project-site__text">
              Aplikacja wykorzystuje serwer Apache wraz z nginx.
            </p>

            <div className="project-site__technologies">
              <p className=" project-site__subtitle ">Technologies:</p>
              <ul className="project-site__list">
                <li className="project__list-item">React JS</li>
                <li className="project__list-item">Node.js</li>
                <li className="project__list-item">Express</li>
                <li className="project__list-item">MongoDB</li>
                <li className="project__list-item">nginx</li>
              </ul>
            </div>
          </div>
        </div>
        <Tabs history={history} />

        {images}
      </motion.div>
      <Contact />
      <Overlay />
      <ScrollUpButton
        ContainerClassName="scrollUpContainer"
        TransitionClassName="scrollUpTransition"
        ShowAtPosition={250}
      >
        <FiChevronUp />
      </ScrollUpButton>
    </>
  );
}

function Tabs({ history }) {
  return (
    <div className="tabs">
      <motion.div
        initial={{
          opacity: 0,
          y: 50,
        }}
        animate={{
          y: 0,
          opacity: 0.7,
          transition: { duration: 0.4, delay: 0.5 },
        }}
        exit={{
          x: -50,
          opacity: 0,
          transition: { duration: 0.2 },
        }}
        transition={{ duration: 0.4 }}
        className="tab tab-left"
        onClick={() => {
          history.push("/");
          setTimeout(() => {
            scroller.scrollTo("project2", {
              smooth: true,
              offset: -80,
              duration: 0,
            });
          }, 3000);
        }}
      >
        <IoIosArrowBack className="tab__icon" />
        <span>projekty</span>
      </motion.div>

      <motion.div
        initial={{
          opacity: 0,
          y: 50,
        }}
        animate={{
          y: 0,
          opacity: 0.7,
        }}
        exit={{
          x: 50,
          opacity: 0,
        }}
        transition={{ duration: 0.4, delay: 0.5 }}
        className="tab tab-right"
        onClick={() => {
          history.push("/ochronne-maski");
        }}
      >
        <span>następny</span>
        <IoIosArrowForward className="tab__icon" />
      </motion.div>
    </div>
  );
}
