import React, { useState } from "react";

export default function About() {
  const [seleted, setSeleted] = useState(0);
  const handleAccordion = (i) =>
    i === seleted ? setSeleted(null) : setSeleted(i);
  return (
    <section id="about" className={"about-wrapper"}>
      <section className={"about"}>
        <div className="about__tech">
          <div className="about__title">Tech stack:</div>
          <div className="about__tech-main">
            <div className="acordion-option">
              <div
                onClick={() => {
                  handleAccordion(0);
                }}
                className={`acordion-title ${seleted === 0 ? "open2" : ""}`}
              >
                <div className="arrow-left"></div>
                <div className="arrow-right"></div> Frontend
              </div>
              <div className={`acordion-menu ${seleted === 0 ? "open" : ""}`}>
                <p className="acordion-list">React</p>
                <p className="acordion-list">Redux</p>
                <p className="acordion-list">Javascript</p>
                <p className="acordion-list">Typescript</p>
                <p className="acordion-list">HTML/CSS</p>
                <p className="acordion-list">SCSS</p>
                <p className="acordion-list">Styled Components</p>
              </div>
            </div>
            <div className="acordion-option">
              <div
                onClick={() => {
                  handleAccordion(1);
                }}
                className={`acordion-title ${seleted === 1 ? "open2" : ""}`}
              >
                <div className="arrow-left"></div>
                <div className="arrow-right"></div> Backend
              </div>
              <div className={`acordion-menu ${seleted === 1 ? "open" : ""}`}>
                <p className="acordion-list">NodeJS</p>
                <p className="acordion-list">Express</p>
                <p className="acordion-list">MongoDB</p>
                <p className="acordion-list">Mongoose</p>
                <p className="acordion-list">JSON Web Token</p>
              </div>
            </div>
            <div className="acordion-option">
              <div
                onClick={() => {
                  handleAccordion(2);
                }}
                className={`acordion-title ${seleted === 2 ? "open2" : ""}`}
              >
                <div className="arrow-left"></div>
                <div className="arrow-right"></div> Tools
              </div>
              <div className={`acordion-menu ${seleted === 2 ? "open" : ""}`}>
                <p className="acordion-list">Git</p>
                <p className="acordion-list">Heroku</p>
                <p className="acordion-list">Figma</p>
                <p className="acordion-list">Visual Studio Code</p>
              </div>
            </div>
          </div>
        </div>
        <div className="about__me">
          <div className="about__title">About me:</div>
          <div className="about__me-main">
            I’m a self-taught developer based in Cracow and a final year student
            of the second-degree studies in Robotics at University of Science
            and Technology, Cracow. Currently, working on freelance projects and
            updating my portfolio.
          </div>
          <div className="about__me-secondary">
            During my studies at high school and college, I studied mainly C++
            and Matlab but I have been seriously working for over a year with
            web development in JavaScript. I love everything to do with
            development &amp; visual design as well as delivering
            extraordinarily creative solutions across digital platforms and
            beyond.
          </div>
        </div>
      </section>
    </section>
  );
}
