import React from "react";
import { IoLogoGithub } from "react-icons/io";
import { FaLinkedinIn } from "react-icons/fa";
import { MdEmail } from "react-icons/md";
import { Link } from "react-scroll";
import dk1 from "../photos/dawid2.png";

export default function Header() {
  return (
    <div className="wrapper">
      <header className="header">
        <div data-speed="3" className="header__call layer">
          <div className="header__text header__text-hi">
            {`Cześć, tutaj Dawid`.split(" ").map((i) => (
              <span className="header__dawid">{i}</span>
            ))}
          </div>
          <div className="header__text">
            {`Jestem Full Stack Deweloperem,`.split(" ").map((i) => (
              <span>{i}</span>
            ))}
          </div>
          <div className="header__text">
            {`zdobywającym doświadczenie`.split(" ").map((i) => (
              <span>{i}</span>
            ))}
          </div>
          <div className="callbacks ">
            <Link
              className="header__button"
              smooth={true}
              offset={-80}
              duration={500}
              to="projects"
            >
              portfolio
            </Link>
          </div>
          <div className="icons">
            <a href="https://github.com/dkurpi" target="_blank">
              <IoLogoGithub className={"icon"} />
            </a>
            <a href="https://www.linkedin.com/in/dkurpi/" target="_blank">
              <FaLinkedinIn className={"icon"} />
            </a>
            <Link smooth={true} offset={-80} duration={1500} to="contact">
              <MdEmail className={"icon"} />
            </Link>
          </div>
        </div>
        <div className="header__image">
          <img src={dk1} alt="" />
        </div>
      </header>
    </div>
  );
}
