import React, { useEffect } from "react";
import { IoLogoGithub } from "react-icons/io";
import { IoIosArrowBack } from "react-icons/io";
import { IoIosArrowForward } from "react-icons/io";
import { scroller } from "react-scroll";
import { useHistory, Link as Link1 } from "react-router-dom";
import { motion } from "framer-motion";
import Zoom from "react-medium-image-zoom";
import "react-medium-image-zoom/dist/styles.css";

import { FiChevronUp } from "react-icons/fi";
import ScrollUpButton from "react-scroll-up-button";

import Contact from "../Contact";
import Overlay from "../Overlay";

import pf1 from "../../photos/pf1.jpg";
import pf2 from "../../photos/pf2.jpg";
import pf3 from "../../photos/pf3.jpg";
import pfg2 from "../../photos/pf11.gif";
import pfg1 from "../../photos/pf12.gif";

const images = [pfg1, pfg2, pf3, pf1, pf2].map((url) => (
  <div className="project-site__image-section">
    <Zoom
      alt="that wanaka tree"
      overlayBgColorStart={"#191919"}
      overlayBgColorEnd={"#191919"}
      closeText="3213123"
      openText="3213123"
      zoomMargin={40}
    >
      <img src={url} className="project-site__image" />
    </Zoom>
  </div>
));

export default function OnlineLibrary() {
  let history = useHistory();
  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);
  useEffect(() => {
    setTimeout(() => {}, 1000);
    return () => {};
  });

  return (
    <>
      <motion.div className="project-site-wrapper">
        <div className="project-site__name">
          <h1 className="project-site__title">
            <span>{`Path`}</span>
            Finder
          </h1>
          <div className="project-site__date">April 2020</div>
        </div>

        <div className="project-site">
          <div className="project-site__info">
            <p className="project-site__main-title">About:</p>
            <p className="project-site__text">
              Project designed to visualize pathfinding algorithm (the Dijkstra
              algorithm) and the maze generator.
            </p>

            <ul className="project-site__ulist">
              <li className="project-site__ulist-item">
                create complicated walls patterns
              </li>
              <li className="project-site__ulist-item">generate random maze</li>
              <li className="project-site__ulist-item">
                find the shortest path to every pattern
              </li>
              <li className="project-site__ulist-item">
                interactive search for a new path after adding a new wall
              </li>
              <li className="project-site__ulist-item">
                set start and end point
              </li>
            </ul>

            <div className="project-site__technologies">
              <ul
                style={{ paddingTop: "4vh" }}
                className="project-site__list project-site__list--pages"
              >
                <a
                  href="https://dnt-path.herokuapp.com/"
                  className="project__list-item--more"
                  target="_blank"
                >
                  Visit site
                </a>
                <a
                  href="https://github.com/dkurpi/path-finder"
                  className="project-site__list-item--more"
                  target="_blank"
                >
                  <IoLogoGithub className="github-logo" />
                </a>
              </ul>
            </div>
          </div>

          <div className="project-site__info project-site__info--border">
            <p className="project-site__main-title">Tech wrap-up:</p>
            <p className="project-site__text">
              The project implements Dijkstra algorithm for finding the shortest
              path {"&"} Prim's algorithm for maze generator.
            </p>

            <ul className="project-site__ulist">
              <li className="project-site__ulist-item">app made in ReactJS</li>
              <li className="project-site__ulist-item">
                all animations and styling in plain CSS
              </li>
              <li className="project-site__ulist-item">
                algorithms implemented in plain JS
              </li>
            </ul>

            <div className="project-site__technologies">
              <p className=" project-site__subtitle ">Technologies:</p>
              <ul className="project-site__list">
                <li className="project__list-item">React JS</li>
                <li className="project__list-item">JavaScript</li>
                <li className="project__list-item">CSS</li>
              </ul>
            </div>
          </div>
        </div>
        <Tabs history={history} />

        {images}
      </motion.div>
      <Contact />
      <Overlay />
      <ScrollUpButton
        ContainerClassName="scrollUpContainer"
        TransitionClassName="scrollUpTransition"
        ShowAtPosition={250}
      >
        <FiChevronUp />
      </ScrollUpButton>
    </>
  );
}

function Tabs({ history }) {
  return (
    <div className="tabs">
      <motion.div
        initial={{
          opacity: 0,
          y: 50,
        }}
        animate={{
          y: 0,
          opacity: 0.7,
          transition: { duration: 0.4, delay: 0.5 },
        }}
        exit={{
          x: -50,
          opacity: 0,
          transition: { duration: 0.2 },
        }}
        transition={{ duration: 0.4 }}
        className="tab tab-left"
        onClick={() => {
          history.push("/");
          setTimeout(() => {
            scroller.scrollTo("project5", {
              smooth: true,
              offset: -80,
              duration: 0,
            });
          }, 3000);
        }}
      >
        <IoIosArrowBack className="tab__icon" />
        <span>portfolio</span>
      </motion.div>

      <motion.div
        initial={{
          opacity: 0,
          y: 50,
        }}
        animate={{
          y: 0,
          opacity: 0.7,
        }}
        exit={{
          x: 50,
          opacity: 0,
        }}
        transition={{ duration: 0.4, delay: 0.5 }}
        className="tab tab-right"
        onClick={() => {
          history.push("/online-library");
        }}
      >
        <span>next project</span>

        <IoIosArrowForward className="tab__icon" />
      </motion.div>
    </div>
  );
}
