import React from "react";
import { Link } from "react-router-dom";
import { animatePopup } from "../utils/animationsSetup";

import ol1 from "../photos/ol1.jpg";
import ol2 from "../photos/ol2.jpg";

import hk1 from "../photos/hk1.jpg";
import hk2 from "../photos/hk2.jpg";

import om1 from "../photos/om1.jpg";
import om2 from "../photos/om2.jpg";

import pf1 from "../photos/pf12.gif";

import vg1 from "../photos/vg1.gif";

const ol = [
  <div id="project1" style={{ paddingTop: "8vh" }} className="project">
    <div className="project__info">
      <div className="project__date">July 2020 - October 2020</div>
      <h1 className="project__title ">
        <span>online</span>
        LIBRARY
      </h1>
      <p className="project__text">
        A college project which allows users to share their opinions about the
        books they read. The application contains interactions between users,
        they can publish, mark, save, rate, comment the books. The application
        contains statistics of books added by users, on the basis of which the
        best books are selected.
        <br />
      </p>
      <div className="project__technologies">
        <ul className="project__list project__list--pages">
          <a
            href="https://isr-project-kurpiel.herokuapp.com/"
            target="_blank"
            className="project__list-item--more"
          >
            Visit site
          </a>

          <Link className="project__list-item--more" to="/online-library">
            Learn more
          </Link>
        </ul>
      </div>
      <div className="project__technologies">
        <p className="project__subtitle">Technologies:</p>
        <ul className="project__list">
          <li className="project__list-item">React JS</li>
          <li className="project__list-item">Redux</li>
          <li className="project__list-item">Node.js</li>
          <li className="project__list-item">Express</li>
          <li className="project__list-item">MongoDB</li>
          <li className="project__list-item">JWT</li>
        </ul>
      </div>
    </div>
    <div onClick={animatePopup} className="project__image-section">
      <Link to="/online-library">
        <img
          className="project__image"
          src={ol1}
          onMouseEnter={(e) => {
            e.target.src = ol2;
          }}
          onMouseLeave={(e) => {
            e.target.src = ol1;
          }}
        />
      </Link>
    </div>
  </div>,
];
const vg = [
  <div id="project2" className="project">
    <div className="project__info">
      <div className="project__date">February 2020 - April 2020</div>
      <h1 className="project__title ">Velluto Giorno</h1>
      <p className="project__text">
        Online store for a local clothing company. The application allows the
        user to find desired items, select the size, add them to the cart, and
        place orders. The admin panel allows handling orders and manages items.
        <br />
      </p>
      <div className="project__technologies">
        <ul className="project__list project__list--pages">
          <a
            href="http://vellutogiorno.pl/"
            target="_blank"
            className="project__list-item--more"
          >
            Visit site
          </a>

          <Link className="project__list-item--more" to="/vellutogiorno">
            Learn more
          </Link>
        </ul>
      </div>
      <div className="project__technologies">
        <p className="project__subtitle">Technologies:</p>
        <ul className="project__list">
          <li className="project__list-item">React JS</li>
          <li className="project__list-item">Node.js</li>
          <li className="project__list-item">Express</li>
          <li className="project__list-item">MongoDB</li>
          <li className="project__list-item">nginx</li>
        </ul>
      </div>
    </div>
    <div onClick={animatePopup} className="project__image-section">
      <Link style={{ width: "100%" }} to="/vellutogiorno">
        <img className="project__image" src={vg1} />
      </Link>
    </div>
  </div>,
];
const om = [
  <div id="project3" className="project">
    <div className="project__info">
      <div className="project__date">March 2020 - April 2020</div>
      <h1 className="project__title ">
        <span>{`Ochronne Maski`}</span>
      </h1>
      <p className="project__text">
        E-commerce website for a local company selling reusable masks in various
        designs and materials. The application allows users to buy masks, even
        in larger quantities. The admin panel allows handling orders status.
        <br />
      </p>
      <div className="project__technologies">
        <ul className="project__list project__list--pages">
          <a
            href="https://maseczkiochronne.herokuapp.com/"
            target="_blank"
            className="project__list-item--more"
          >
            Visit site
          </a>

          <Link className="project__list-item--more" to="/ochronne-maski">
            Learn more
          </Link>
        </ul>
      </div>
      <div className="project__technologies">
        <p className="project__subtitle">Technologies:</p>
        <ul className="project__list">
          <li className="project__list-item">React JS</li>
          <li className="project__list-item">Node.js</li>
          <li className="project__list-item">Express</li>
          <li className="project__list-item">MongoDB</li>
          <li className="project__list-item">Bootstrap</li>
        </ul>
      </div>
    </div>
    <div onClick={animatePopup} className="project__image-section">
      <Link to="/ochronne-maski">
        <img
          className="project__image"
          src={om1}
          onMouseEnter={(e) => {
            e.target.src = om2;
          }}
          onMouseLeave={(e) => {
            e.target.src = om1;
          }}
        />
      </Link>
    </div>
  </div>,
];

const HK = [
  <div id="project4" className="project">
    <div className="project__info">
      <div className="project__date">May 2020 - July 2020</div>
      <h1 className="project__title ">
        <span>{`HYDRO`}</span>
        KRAK
      </h1>
      <p className="project__text">
        Website of the hydraulic company HydroKrak based in Cracow. Contains
        visual effects like scroll animations, page transitions also allows
        users to send e-mails via form directly to the company's mailbox.
        <br />
      </p>
      <div className="project__technologies">
        <ul className="project__list project__list--pages">
          <a
            href="http://www.hydro-krak.pl/"
            target="_blank"
            className="project__list-item--more"
          >
            Visit site
          </a>

          <Link className="project__list-item--more" to="/hydro-krak">
            Learn more
          </Link>
        </ul>
      </div>
      <div className="project__technologies">
        <p className="project__subtitle">Technologies:</p>
        <ul className="project__list">
          <li className="project__list-item">React JS</li>
          <li className="project__list-item">SCSS</li>
          <li className="project__list-item">GSAP</li>
          <li className="project__list-item">EmailJS</li>
        </ul>
      </div>
    </div>
    <div onClick={animatePopup} className="project__image-section">
      <Link to="/hydro-krak">
        <img
          className="project__image"
          src={hk1}
          onMouseEnter={(e) => {
            e.target.src = hk2;
          }}
          onMouseLeave={(e) => {
            e.target.src = hk1;
          }}
        />
      </Link>
    </div>
  </div>,
];

const pf = [
  <div id="project5" className="project">
    <div className="project__info">
      <div className="project__date">April 2020</div>
      <h1 className="project__title ">
        <span>{`Path`}</span>
        Finder
      </h1>
      <p className="project__text">
        Project designed to visualize pathfinding algorithm (the Dijkstra
        algorithm) and the maze generator. Allows the user to interact with the
        board, create walls, complicated patterns.
        <br />
      </p>
      <div className="project__technologies">
        <ul className="project__list project__list--pages">
          <a
            href="https://dnt-path.herokuapp.com/"
            className="project__list-item--more"
            target="_blank"
          >
            Project
          </a>
          <Link className="project__list-item--more" to="/pathfinder">
            Learn more
          </Link>
        </ul>
      </div>
      <div className="project__technologies">
        <p className="project__subtitle">Technologies:</p>
        <ul className="project__list">
          <li className="project__list-item">React JS</li>
          <li className="project__list-item">Javascript</li>
          <li className="project__list-item">CSS</li>
        </ul>
      </div>
    </div>
    <div onClick={animatePopup} className="project__image-section">
      <Link style={{ width: "100%" }} to="/pathfinder">
        <img className="project__image" src={pf1} />
      </Link>
    </div>
  </div>,
];
export default function Projects() {
  return (
    <>
      <div id="projects" className="projects__title">
        <div className="projects__title-text">My projects</div>
      </div>
      <div className="projects">
        {ol}
        {vg}
        {om}
        {HK}
        {pf}
      </div>
    </>
  );
}
