import React, { useEffect, useState } from "react";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";

import Navigation from "./Components/Navigation";
import MainPage from "./Components/MainPage";
import OnlineLibrary from "./Components/Pages/OnlineLibrary";
import VellutoGiorno from "./Components/Pages/VellutoGiorno";
import OchronneMaski from "./Components/Pages/OchronneMaski";
import HydroKrak from "./Components/Pages/HydroKrak";
import PathFinder from "./Components/Pages/PathFinder";

import MainPagePL from "./ComponentsPL/MainPage";
import OnlineLibraryPL from "./ComponentsPL/Pages/OnlineLibrary";
import VellutoGiornoPL from "./ComponentsPL/Pages/VellutoGiorno";
import OchronneMaskiPL from "./ComponentsPL/Pages/OchronneMaski";
import HydroKrakPL from "./ComponentsPL/Pages/HydroKrak";
import PathFinderPL from "./ComponentsPL/Pages/PathFinder";

import animationsSetup from "./utils/animationsSetup";
import { cursorSetup, parallax } from "./utils/cursorSetup";

import { AnimatePresence } from "framer-motion";

function App() {
  const [isFirstTime, setIsFirstTime] = useState(true);
  const [isEnglish, setIsEnglish] = useState(true);

  useEffect(() => {
    if (isFirstTime) {
      animationsSetup(() => {
        cursorSetup();
        parallax();
        setIsFirstTime(false);
      });
    }
  });

  return (
    <Router>
      <Navigation isEnglish={isEnglish} setIsEnglish={setIsEnglish} />

      {isEnglish ? (
        <Route
          render={({ location }) => (
            <AnimatePresence exitBeforeEnter initial={false}>
              <Switch location={location} key={location.pathname}>
                <Route exact path="/" component={MainPage} />
                <Route exact path="/online-library" component={OnlineLibrary} />
                <Route exact path="/vellutogiorno" component={VellutoGiorno} />
                <Route exact path="/ochronne-maski" component={OchronneMaski} />
                <Route exact path="/hydro-krak" component={HydroKrak} />
                <Route exact path="/pathfinder" component={PathFinder} />
                <Route
                  path="*"
                  component={() => {
                    window.location.href = "/";
                  }}
                />
              </Switch>
            </AnimatePresence>
          )}
        />
      ) : (
        <Route
          render={({ location }) => (
            <AnimatePresence exitBeforeEnter initial={false}>
              <Switch location={location} key={location.pathname}>
                <Route exact path="/" component={MainPagePL} />
                <Route
                  exact
                  path="/online-library"
                  component={OnlineLibraryPL}
                />
                <Route
                  exact
                  path="/vellutogiorno"
                  component={VellutoGiornoPL}
                />
                <Route
                  exact
                  path="/ochronne-maski"
                  component={OchronneMaskiPL}
                />
                <Route exact path="/hydro-krak" component={HydroKrakPL} />
                <Route exact path="/pathfinder" component={PathFinderPL} />
                <Route
                  path="*"
                  component={() => {
                    window.location.href = "/";
                  }}
                />
              </Switch>
            </AnimatePresence>
          )}
        />
      )}
      {isFirstTime && (
        <div className="overlay">
          <div className="overlay__section left" />
          <div className="overlay__section right" />
          <div className="overlay__section left" />
          <div className="overlay__section right" />
        </div>
      )}
      <div className="cursor1" />
      <div className="cursor2" />
    </Router>
  );
}

export default App;
