import React, { useEffect } from "react";
import { IoLogoGithub } from "react-icons/io";
import { IoIosArrowBack } from "react-icons/io";
import { IoIosArrowForward } from "react-icons/io";
import { scroller } from "react-scroll";
import { useHistory, Link as Link1 } from "react-router-dom";
import { motion } from "framer-motion";
import Zoom from "react-medium-image-zoom";
import "react-medium-image-zoom/dist/styles.css";

import { FiChevronUp } from "react-icons/fi";
import ScrollUpButton from "react-scroll-up-button";

import Contact from "../Contact";
import Overlay from "../Overlay";

import ol1 from "../../photos/ol1.jpg";
import ol3 from "../../photos/ol3.jpg";
import ol4 from "../../photos/ol4.jpg";
import ol5 from "../../photos/ol5.jpg";
import ol6 from "../../photos/ol6.jpg";
import olstats from "../../photos/olstats.jpg";

const images = [ol1, ol3, ol4, ol5, olstats, ol6].map((url) => (
  <div className="project-site__image-section">
    <Zoom
      alt="that wanaka tree"
      overlayBgColorStart={"#191919"}
      overlayBgColorEnd={"#191919"}
      closeText="3213123"
      openText="3213123"
      zoomMargin={40}
    >
      <img src={url} className="project-site__image" />
    </Zoom>
  </div>
));

export default function OnlineLibrary() {
  let history = useHistory();
  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);
  useEffect(() => {
    setTimeout(() => {
      console.log("mounted");
    }, 1000);
    return () => {
      console.log("exit");
    };
  });

  return (
    <>
      <motion.div className="project-site-wrapper">
        <div className="project-site__name">
          <h1 className="project-site__title">
            <span>online</span>
            LIBRARY
          </h1>
          <div className="project-site__date">July 2020 - October 2020</div>
        </div>

        <div className="project-site">
          <div className="project-site__info">
            <p className="project-site__main-title">About:</p>
            <p className="project-site__text">
              A college project which allows users to share their opinions about
              the books they read. I had built and deploy the entire platform
              from scratch.
            </p>
            <p className="project-site__text bold">
              The application alows user to:
            </p>

            <ul className="project-site__ulist">
              <li className="project-site__ulist-item">browse books</li>
              <li className="project-site__ulist-item">
                filter books by category, pages, year, name
              </li>
              <li className="project-site__ulist-item">
                share your books review
              </li>
              <li className="project-site__ulist-item">
                rate and comment on the books
              </li>
              <li className="project-site__ulist-item">
                like/dislike the comment of book
              </li>
              <li className="project-site__ulist-item">
                browse best books, by most popular, and the best rate
              </li>
              <li className="project-site__ulist-item">
                mark books to read later
              </li>
              <li className="project-site__ulist-item">
                delete books which you added
              </li>
              <li className="project-site__ulist-item">
                watch the details of each user
              </li>
            </ul>
            <div className="project-site__technologies">
              <ul className="project-site__list project-site__list--pages">
                <a
                  href="https://isr-project-kurpiel.herokuapp.com/"
                  className="project-site__list-item--more"
                  target="_blank"
                >
                  Visit site
                </a>
                <a
                  href="https://github.com/dkurpi/online-library"
                  className="project-site__list-item--more"
                  target="_blank"
                >
                  <IoLogoGithub className="github-logo" />
                </a>
              </ul>
            </div>
          </div>

          <div className="project-site__info project-site__info--border">
            <p className="project-site__main-title">Tech wrap-up:</p>
            <p className="project-site__text">
              Full-stack application utilizing a MERN stack.
            </p>
            <p className="project-site__subtitle project-site__subtitle--big">
              Front-end App
            </p>

            <ul className="project-site__ulist">
              <li className="project-site__ulist-item">
                SPA done in React {`&`} Redux
              </li>
              <li className="project-site__ulist-item">
                implements Material-UI
              </li>
              <li className="project-site__ulist-item">
                sends API requests to the server
              </li>
            </ul>

            <p className="project-site__subtitle project-site__subtitle--big">
              Back-end Server
            </p>

            <ul className="project-site__ulist">
              <li className="project-site__ulist-item">
                done in NodeJS with MongoDB
              </li>
              <li className="project-site__ulist-item">
                handles all the API requests
              </li>
              <li className="project-site__ulist-item">
                serves front-end web app
              </li>
              <li className="project-site__ulist-item">
                implements Json Web Tokens for authentication
              </li>
            </ul>
            <p className="project-site__text">
              Application is automatically deployable via Git to Heroku.
            </p>

            <div className="project-site__technologies">
              <p className=" project-site__subtitle ">Technologies:</p>
              <ul className="project-site__list">
                <li className="project-site__list-item">React JS</li>
                <li className="project-site__list-item">Redux</li>
                <li className="project-site__list-item">Node.js</li>
                <li className="project-site__list-item">Express</li>
                <li className="project-site__list-item">MongoDB</li>
                <li className="project-site__list-item">Material-UI</li>
                <li className="project-site__list-item">JWT</li>
              </ul>
            </div>
          </div>
        </div>
        <Tabs history={history} />

        {images}
      </motion.div>
      <Contact />
      <Overlay />
      <ScrollUpButton
        ContainerClassName="scrollUpContainer"
        TransitionClassName="scrollUpTransition"
        ShowAtPosition={250}
      >
        <FiChevronUp />
      </ScrollUpButton>
    </>
  );
}

function Tabs({ history }) {
  return (
    <div className="tabs">
      <motion.div
        initial={{
          opacity: 0,
          y: 50,
        }}
        animate={{
          y: 0,
          opacity: 0.7,
          transition: { duration: 0.4, delay: 0.5 },
        }}
        exit={{
          x: -50,
          opacity: 0,
          transition: { duration: 0.2 },
        }}
        transition={{ duration: 0.4 }}
        className="tab tab-left"
        onClick={() => {
          history.push("/");
          setTimeout(() => {
            scroller.scrollTo("projects", {
              smooth: true,
              offset: -80,
              duration: 0,
            });
          }, 3000);
        }}
      >
        <IoIosArrowBack className="tab__icon" />
        <span>portfolio</span>
      </motion.div>

      <motion.div
        initial={{
          opacity: 0,
          y: 50,
        }}
        animate={{
          y: 0,
          opacity: 0.7,
        }}
        exit={{
          x: 50,
          opacity: 0,
        }}
        transition={{ duration: 0.4, delay: 0.5 }}
        className="tab tab-right"
        onClick={() => {
          history.push("/vellutogiorno");
        }}
      >
        <span>next project</span>

        <IoIosArrowForward className="tab__icon" />
      </motion.div>
    </div>
  );
}
