import React, { useState } from "react";
import { Link, scroller } from "react-scroll";
import { useHistory, useLocation } from "react-router-dom";

import Burger from "@animated-burgers/burger-squeeze";
import "@animated-burgers/burger-squeeze/dist/styles.css";

export default function Navigation({ isEnglish, setIsEnglish }) {
  let history = useHistory();
  let location = useLocation();

  const [isOpen, setIsOpen] = useState(false);

  return (
    <div className="navigation-wrapper">
      <nav className="navigation">
        <div className="navigation__logo">
          <Link
            className="navigation__logo-text"
            onClick={() => {
              if (location.pathname === "/")
                return scroller.scrollTo("header", {
                  smooth: true,
                  offset: -150,
                  duration: 500,
                });

              history.push("/");
              setTimeout(() => {
                scroller.scrollTo("header", {
                  smooth: true,
                  offset: -150,
                  duration: 0,
                });
              }, 3000);
            }}
          >
            _dkurpi
          </Link>
        </div>
        <Menu
          desktop
          location={location}
          history={history}
          setIsOpen={setIsOpen}
          isEnglish={isEnglish}
          setIsEnglish={setIsEnglish}
        />
        <div className="navigation__hamburger">
          <Burger
            isOpen={isOpen}
            onClick={() => setIsOpen(!isOpen)}
            className="navigation__hamburger-icon"
            width="48px"
          />
        </div>
      </nav>

      {isOpen && (
        <Menu
          mobile
          location={location}
          history={history}
          setIsOpen={setIsOpen}
          isEnglish={isEnglish}
          setIsEnglish={setIsEnglish}
        />
      )}
    </div>
  );
}

function Menu({
  history,
  location,
  setIsOpen,
  desktop = false,
  mobile = false,
  isEnglish,
  setIsEnglish,
}) {
  return (
    <div
      className={`navigation__menu ${
        desktop ? "navigation__menu--desktop" : ""
      } ${mobile ? "navigation__menu--mobile" : ""}
      `}
    >
      <Link
        className="navigation__menu-item"
        onClick={() => {
          setIsOpen(false);
          if (location.pathname === "/")
            return scroller.scrollTo("about", {
              smooth: true,
              offset: -150,
              duration: 500,
            });

          history.push("/");
          setTimeout(() => {
            scroller.scrollTo("about", {
              smooth: true,
              offset: -150,
              duration: 0,
            });
          }, 3000);
        }}
      >
        {isEnglish ? "About" : "O mnie"}
      </Link>
      <Link
        className="navigation__menu-item"
        onClick={() => {
          setIsOpen(false);
          if (location.pathname === "/")
            return scroller.scrollTo("projects", {
              smooth: true,
              offset: -80,
              duration: 500,
            });

          history.push("/");
          setTimeout(() => {
            scroller.scrollTo("projects", {
              smooth: true,
              offset: -80,
              duration: 0,
            });
          }, 3000);
        }}
      >
        {isEnglish ? "Projects" : "Moje projekty"}
      </Link>

      <Link
        className="navigation__menu-item"
        to="contact"
        spy={true}
        smooth={true}
        offset={-200}
        duration={1000}
        onClick={() => {
          setIsOpen(false);
        }}
      >
        {isEnglish ? "Contact" : "Kontakt"}
      </Link>
      <span className="language">
        <span
          onClick={() => {
            setIsEnglish(true);
            setIsOpen(false);
          }}
          className={`language__item ${isEnglish ? "active" : ""}`}
        >
          EN
        </span>
        {"  "}
        <span
          onClick={() => {
            setIsEnglish(false);
            setIsOpen(false);
          }}
          className={`language__item ${!isEnglish ? "active" : ""}`}
        >
          PL
        </span>
      </span>
    </div>
  );
}
