import { gsap } from "gsap";

export default function animationsSetup(onComplete) {
  let vh = window.innerHeight * 0.01;
  document.documentElement.style.setProperty("--vh", `${vh}px`);
  const tl = gsap.timeline();

  if (document.querySelector(".header__call") === null) return onComplete();

  const [hey, ...rest] = document.querySelector(".header__call").childNodes;
  const dawid = document.querySelectorAll(".header__dawid");
  const overlays = document.querySelectorAll(".overlay__section");

  if (window.scrollY < 10 * vh) {
    tl.to("body", 0, {
      css: { visibility: "visible", overflowY: "hidden", height: "100vh" },
    })
      .to(".header__call", 0, {
        zIndex: 801,
        delay: 2,
      })

      .from(dawid[0], 0.6, {
        y: 100,
        stagger: 0.8,
        ease: "Power4.out",
        skewY: 7,
      })
      .from([dawid[1], dawid[2]], 0.7, {
        y: 100,
        stagger: 0.2,
        ease: "Power4.out",
        skewY: 7,
      })
      .from(overlays, 2, {
        stagger: 0.2,
        height: "100vh",
        ease: "expo.inOut",
      })
      .to(".header__call", 0, {
        zIndex: 105,
      })
      .to("body", 0, {
        overflowY: "scroll",
        height: "auto",
      })
      .from(
        rest,
        1.2,
        {
          y: 100,
          opacity: 0,
          stagger: 0.2,
          ease: "expo.inOut",
        },
        "-=1.5"
      )

      .from(
        ".header__call",
        0.6,
        {
          borderWidth: "0",
          ease: "Power4.in",
        },
        "-=0.5"
      )
      .from(
        ".header__image",
        1,
        {
          filter: "blur(40px)",
          ease: "linear",
        },
        "-=1"
      )

      .from(".header__title", 1, {
        y: 100,
        ease: "expo.inOut",
        onComplete,
      });
  } else {
    tl.to("body", 0, {
      css: { visibility: "visible", overflowY: "hidden", height: "100vh" },
    })

      .to("body", 0, {
        overflowY: "scroll",
        height: "auto",
      })
      .from(overlays, 2, {
        stagger: 0.2,
        height: "100vh",
        ease: "expo.inOut",
        onComplete,
      });
  }
}

export function animatePopup(onComplete) {
  const tl = gsap.timeline();

  const overlays = document.querySelectorAll(".overlay__section");

  tl.to(overlays, 2, {
    stagger: 0.2,
    height: "100vh",
    ease: "expo.inOut",
  });
}
