import react from "react";
import { IoLogoGithub } from "react-icons/io";
import { FaLinkedinIn } from "react-icons/fa";
import { MdEmail } from "react-icons/md";

export default function Contact() {
  return (
    <div id="contact" className="contact-wrapper">
      <section className={"contact"}>
        <div data-speed="2" className="contact__title layer">
          <span className={"contact__absolute"}>Kontakt</span>
        </div>
        <div className="contact__info">
          <a href="https://github.com/dkurpi" target="_blank">
            <div className="contact__social">
              <div className="contact__icon-wrapper">
                <IoLogoGithub className={"contact__icon"} />
              </div>
              <div className="contact__text">/dkurpi</div>
            </div>
          </a>
          <a href="mailto:kurpieldawid@gmail.com">
            <div className="contact__social">
              <div className="contact__icon-wrapper">
                <MdEmail className={"contact__icon"} />
              </div>
              <div className="contact__text">kurpieldawid@gmail.com</div>
            </div>
          </a>
          <a href="https://www.linkedin.com/in/dkurpi/" target="_blank">
            <div className="contact__social">
              <div className="contact__icon-wrapper">
                <FaLinkedinIn className={"contact__icon"} />
              </div>
              <div className="contact__text">/dkurpi</div>
            </div>
          </a>
        </div>
      </section>
    </div>
  );
}
