import React, { useEffect } from "react";
import { IoLogoGithub } from "react-icons/io";
import { IoIosArrowBack } from "react-icons/io";
import { IoIosArrowForward } from "react-icons/io";
import { scroller } from "react-scroll";
import { useHistory, Link as Link1 } from "react-router-dom";
import { motion } from "framer-motion";
import Zoom from "react-medium-image-zoom";
import "react-medium-image-zoom/dist/styles.css";

import { FiChevronUp } from "react-icons/fi";
import ScrollUpButton from "react-scroll-up-button";

import Contact from "../Contact";
import Overlay from "../Overlay";

import om3 from "../../photos/om2.jpg";
import om4 from "../../photos/om4.jpg";
import om5 from "../../photos/om5.jpg";
import om6 from "../../photos/om6.jpg";

const images = [om3, om4, om5, om6].map((url) => (
  <div className="project-site__image-section">
    <Zoom
      alt="that wanaka tree"
      overlayBgColorStart={"#191919"}
      overlayBgColorEnd={"#191919"}
      closeText="3213123"
      openText="3213123"
      zoomMargin={40}
    >
      <img src={url} className="project-site__image" />
    </Zoom>
  </div>
));

export default function OnlineLibrary() {
  let history = useHistory();
  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);
  useEffect(() => {
    setTimeout(() => {}, 1000);
    return () => {};
  });

  return (
    <>
      <motion.div className="project-site-wrapper">
        <div className="project-site__name">
          <h1 className="project-site__title">Ochronne maski</h1>
          <div className="project-site__date">March 2020 - April 2020</div>
        </div>

        <div className="project-site">
          <div className="project-site__info">
            <p className="project-site__main-title">About:</p>
            <p className="project-site__text">
              E-commerce website for a local company selling reusable masks in
              various designs and materials. I had built and deploy the entire
              platform from scratch.
            </p>
            <p className="project-site__text bold">
              The application al;ows user to:
            </p>

            <ul className="project-site__ulist">
              <li className="project-site__ulist-item">browse masks</li>
              <li className="project-site__ulist-item">
                manage cart operation (adding, deleting)
              </li>
              <li className="project-site__ulist-item">
                place orders with the form validation
              </li>
              <li className="project-site__ulist-item">
                use of discounts in case of order larger quantity of masks
              </li>
            </ul>
            <p className="project-site__text bold">Orders panel allows to:</p>
            <ul className="project-site__ulist">
              <li className="project-site__ulist-item">manage placed orders</li>
              <li className="project-site__ulist-item">
                manage the status of orders
              </li>
            </ul>
            <div className="project-site__technologies">
              <ul className="project-site__list project-site__list--pages">
                <a
                  href="https://maseczkiochronne.herokuapp.com/"
                  className="project-site__list-item--more"
                  target="_blank"
                >
                  Visit site
                </a>
                <a
                  href="https://github.com/dkurpi/ochronne-maski"
                  className="project-site__list-item--more"
                  target="_blank"
                >
                  <IoLogoGithub className="github-logo" />
                </a>
              </ul>
            </div>
          </div>

          <div className="project-site__info project-site__info--border">
            <p className="project-site__main-title">Tech wrap-up:</p>
            <p className="project-site__text">
              Full-stack application utilising a MERN stack.
            </p>
            <p className="project-site__subtitle project-site__subtitle--big">
              Front-end App
            </p>

            <ul className="project-site__ulist">
              <li className="project-site__ulist-item">
                SPA done in React {"&"} plain CSS
              </li>
              <li className="project-site__ulist-item">
                implements form validations
              </li>
              <li className="project-site__ulist-item">uses Bootstrap</li>
              <li className="project-site__ulist-item">
                sends API requests to the server
              </li>
            </ul>

            <p className="project-site__subtitle project-site__subtitle--big">
              Back-end Server
            </p>

            <ul className="project-site__ulist">
              <li className="project-site__ulist-item">
                done in NodeJS with MongoDB
              </li>
              <li className="project-site__ulist-item">
                handles all the API requests
              </li>
              <li className="project-site__ulist-item">
                serves front-end web app
              </li>
            </ul>
            <p className="project-site__text">
              Application is automatically deployable via Git to Heroku.
            </p>

            <div className="project-site__technologies">
              <p className=" project-site__subtitle ">Technologies:</p>
              <ul className="project-site__list">
                <li className="project__list-item">React JS</li>
                <li className="project__list-item">Node.js</li>
                <li className="project__list-item">Express</li>
                <li className="project__list-item">MongoDB</li>
                <li className="project__list-item">Bootstrap</li>
              </ul>
            </div>
          </div>
        </div>
        <Tabs history={history} />

        {images}
      </motion.div>
      <Contact />
      <Overlay />
      <ScrollUpButton
        ContainerClassName="scrollUpContainer"
        TransitionClassName="scrollUpTransition"
        ShowAtPosition={250}
      >
        <FiChevronUp />
      </ScrollUpButton>
    </>
  );
}

function Tabs({ history }) {
  return (
    <div className="tabs">
      <motion.div
        initial={{
          opacity: 0,
          y: 50,
        }}
        animate={{
          y: 0,
          opacity: 0.7,
          transition: { duration: 0.4, delay: 0.5 },
        }}
        exit={{
          x: -50,
          opacity: 0,
          transition: { duration: 0.2 },
        }}
        transition={{ duration: 0.4 }}
        className="tab tab-left"
        onClick={() => {
          history.push("/");
          setTimeout(() => {
            scroller.scrollTo("project3", {
              smooth: true,
              offset: -80,
              duration: 0,
            });
          }, 3000);
        }}
      >
        <IoIosArrowBack className="tab__icon" />
        <span>portfolio</span>
      </motion.div>

      <motion.div
        initial={{
          opacity: 0,
          y: 50,
        }}
        animate={{
          y: 0,
          opacity: 0.7,
        }}
        exit={{
          x: 50,
          opacity: 0,
        }}
        transition={{ duration: 0.4, delay: 0.5 }}
        className="tab tab-right"
        onClick={() => {
          history.push("/hydro-krak");
        }}
      >
        <span>next project</span>

        <IoIosArrowForward className="tab__icon" />
      </motion.div>
    </div>
  );
}
