import React from "react";
import { motion } from "framer-motion";

export default function Overlay() {
  const mid = 0.7;
  const start = 0.5;
  const end = 0.9;
  const duration = 1.2;
  return (
    <motion.div className="overlay">
      <motion.div
        initial={{
          height: "100vh",
        }}
        animate={{
          height: 0,
          transition: { duration, delay: mid },
        }}
        exit={{
          height: "100vh",
          transition: { duration, delay: start },
        }}
        className="overlay__section left light"
      />
      <motion.div
        initial={{
          height: "100vh",
        }}
        animate={{
          height: 0,
          transition: { duration, delay: start },
        }}
        exit={{
          height: "100vh",
          transition: { duration, delay: mid },
        }}
        className="overlay__section left dark"
      />
      <motion.div
        initial={{
          height: "100vh",
        }}
        animate={{
          height: 0,
          transition: { duration, delay: end },
        }}
        exit={{
          height: "100vh",
          transition: { duration, delay: mid },
        }}
        className="overlay__section right light"
      />
      <motion.div
        initial={{
          height: "100vh",
        }}
        animate={{
          height: 0,
          transition: { duration, delay: mid },
        }}
        exit={{
          height: "100vh",
          transition: { duration, delay: end },
        }}
        className="overlay__section right dark"
      />
    </motion.div>
  );
}
