import React from "react";
import { Link } from "react-router-dom";
import { animatePopup } from "../utils/animationsSetup";

import { IoIosKeypad as Page } from "react-icons/io";
import { SiPagekit as Info } from "react-icons/si";

import ol1 from "../photos/ol1.jpg";
import ol2 from "../photos/ol2.jpg";

import hk1 from "../photos/hk1.jpg";
import hk2 from "../photos/hk2.jpg";

import om1 from "../photos/om1.jpg";
import om2 from "../photos/om2.jpg";

import pf1 from "../photos/pf12.gif";

import vg1 from "../photos/vg1.gif";

const ol = [
  <div id="project1" style={{ paddingTop: "8vh" }} className="project">
    <div className="project__info">
      <div className="project__date">Lipiec 2020 - Październik 2020</div>
      <h1 className="project__title ">
        <span>online</span>
        LIBRARY
      </h1>
      <p className="project__text">
        Projekt na zaliczenie przedmiotu na studiach, który umożliwia
        użytkownikom dzielenie się opiniami na temat książek, które przeczytali.
        Aplikacja pozwala na interakcje pomiędzy użytkownikami, mogą oni
        publikować, oznaczać, zapisywać, oceniać, komentować książki. Aplikacja
        zawiera statystyki dotyczące książek dodanych przez użytkowników oraz
        selekcje najlepszych książek.
        <br />
      </p>
      <div className="project__technologies">
        <ul className="project__list project__list--pages">
          <a
            href="https://isr-project-kurpiel.herokuapp.com/"
            target="_blank"
            className="project__list-item--more pl"
          >
            Zobacz projekt
          </a>

          <Link className="project__list-item--more pl" to="/online-library">
            Szczegóły...
          </Link>
        </ul>
      </div>
      <div className="project__technologies">
        <p className="project__subtitle">Technologie:</p>
        <ul className="project__list">
          <li className="project__list-item">React JS</li>
          <li className="project__list-item">Redux</li>
          <li className="project__list-item">Node.js</li>
          <li className="project__list-item">Express</li>
          <li className="project__list-item">MongoDB</li>
          <li className="project__list-item">JWT</li>
        </ul>
      </div>
    </div>
    <div onClick={animatePopup} className="project__image-section">
      <Link to="/online-library">
        <img
          className="project__image"
          src={ol1}
          onMouseEnter={(e) => {
            e.target.src = ol2;
          }}
          onMouseLeave={(e) => {
            e.target.src = ol1;
          }}
        />
      </Link>
    </div>
  </div>,
];
const vg = [
  <div id="project2" className="project">
    <div className="project__info">
      <div className="project__date">Luty 2020 - Kwiecień 2020</div>
      <h1 className="project__title ">Velluto Giorno</h1>
      <p className="project__text">
        Sklep internetowy dla lokalnej firmy odzieżowej. Aplikacja umożliwia
        użytkownikowi przeglądanie oferty sklepu, wyszukanie pożądanych
        przedmiotów, wybranie rozmiaru, dodanie do koszyka i złożenie
        zamówienia. Panel administracyjny umożliwia obsługę zamówień i
        zarządzanie ofertą produktów.
        <br />
      </p>
      <div className="project__technologies">
        <ul className="project__list project__list--pages">
          <a
            href="http://vellutogiorno.pl/"
            target="_blank"
            className="project__list-item--more pl"
          >
            Zobacz projekt
          </a>

          <Link className="project__list-item--more pl" to="/vellutogiorno">
            Szczegóły...
          </Link>
        </ul>
      </div>
      <div className="project__technologies">
        <p className="project__subtitle">Technologie:</p>
        <ul className="project__list">
          <li className="project__list-item">React JS</li>
          <li className="project__list-item">Node.js</li>
          <li className="project__list-item">Express</li>
          <li className="project__list-item">MongoDB</li>
          <li className="project__list-item">nginx</li>
        </ul>
      </div>
    </div>
    <div onClick={animatePopup} className="project__image-section">
      <Link style={{ width: "100%" }} to="/vellutogiorno">
        <img className="project__image" src={vg1} />
      </Link>
    </div>
  </div>,
];
const om = [
  <div id="project3" className="project">
    <div className="project__info">
      <div className="project__date">Marzec 2020 - Kwiecień 2020</div>
      <h1 className="project__title ">
        <span>{`Ochronne Maski`}</span>
      </h1>
      <p className="project__text">
        Sklep internetowy dla lokalnej firmy sprzedającej maski wielokrotnego
        użytku w różnych wzorach oraz z różnych materiałów. Aplikacja umożliwia
        zakup masek hurtowo, jak i w mniejszych ilościach. Zaimplementowany
        algorytm automatycznie ustala cenę z promocjami. Panel administracyjny
        pozwala na obsługę statusu zamówień.
        <br />
      </p>
      <div className="project__technologies">
        <ul className="project__list project__list--pages">
          <a
            href="https://maseczkiochronne.herokuapp.com/"
            target="_blank"
            className="project__list-item--more pl"
          >
            Zobacz projekt
          </a>

          <Link className="project__list-item--more pl" to="/ochronne-maski">
            Szczegóły...
          </Link>
        </ul>
      </div>
      <div className="project__technologies">
        <p className="project__subtitle">Technologie:</p>
        <ul className="project__list">
          <li className="project__list-item">React JS</li>
          <li className="project__list-item">Node.js</li>
          <li className="project__list-item">Express</li>
          <li className="project__list-item">MongoDB</li>
          <li className="project__list-item">Bootstrap</li>
        </ul>
      </div>
    </div>
    <div onClick={animatePopup} className="project__image-section">
      <Link to="/ochronne-maski">
        <img
          className="project__image"
          src={om1}
          onMouseEnter={(e) => {
            e.target.src = om2;
          }}
          onMouseLeave={(e) => {
            e.target.src = om1;
          }}
        />
      </Link>
    </div>
  </div>,
];

const HK = [
  <div id="project4" className="project">
    <div className="project__info">
      <div className="project__date">Maj 2020 - Czerwiec 2020</div>
      <h1 className="project__title ">
        <span>{`HYDRO`}</span>
        KRAK
      </h1>
      <p className="project__text">
        Strona internetowa firmy hydraulicznej HydroKrak z siedzibą w Krakowie.
        Strona zawiera liczne efekty wizualne, takie jak animacje podczas
        przewijania. Użytkownicy mogą wysyłać e-maile za pośrednictwem
        formularza bezpośrednio do firmowej skrzynki pocztowej.
        <br />
      </p>
      <div className="project__technologies">
        <ul className="project__list project__list--pages">
          <a
            href="http://www.hydro-krak.pl/"
            target="_blank"
            className="project__list-item--more pl"
          >
            Zobacz projekt
          </a>

          <Link className="project__list-item--more pl" to="/hydro-krak">
            Szczegóły...
          </Link>
        </ul>
      </div>
      <div className="project__technologies">
        <p className="project__subtitle">Technologie:</p>
        <ul className="project__list">
          <li className="project__list-item">React JS</li>
          <li className="project__list-item">SCSS</li>
          <li className="project__list-item">GSAP</li>
          <li className="project__list-item">EmailJS</li>
        </ul>
      </div>
    </div>
    <div onClick={animatePopup} className="project__image-section">
      <Link to="/hydro-krak">
        <img
          className="project__image"
          src={hk1}
          onMouseEnter={(e) => {
            e.target.src = hk2;
          }}
          onMouseLeave={(e) => {
            e.target.src = hk1;
          }}
        />
      </Link>
    </div>
  </div>,
];

const pf = [
  <div id="project5" className="project">
    <div className="project__info">
      <div className="project__date">Kwiecień 2020</div>
      <h1 className="project__title ">
        <span>{`Path`}</span>
        Finder
      </h1>
      <p className="project__text">
        Projekt mający na celu wizualizację wyszukiwania najkrótszej drogi
        między dwoma punktami (algorytm Dijkstry ) oraz dodatkowo został
        Zaimplementowany generator labiryntu. Aplikacja umożliwia użytkownikowi
        interakcję z planszą, na której można, tworzyć ściany, skomplikowane
        wzory, labirynty.
        <br />
      </p>
      <div className="project__technologies">
        <ul className="project__list project__list--pages">
          <a
            href="https://dnt-path.herokuapp.com/"
            className="project__list-item--more pl"
            target="_blank"
          >
            Zobacz projekt
          </a>
          <Link className="project__list-item--more pl" to="/pathfinder">
            Szczegóły...
          </Link>
        </ul>
      </div>
      <div className="project__technologies">
        <p className="project__subtitle">Technologie:</p>
        <ul className="project__list">
          <li className="project__list-item">React JS</li>
          <li className="project__list-item">Javascript</li>
          <li className="project__list-item">CSS</li>
        </ul>
      </div>
    </div>
    <div onClick={animatePopup} className="project__image-section">
      <Link style={{ width: "100%" }} to="/pathfinder">
        <img className="project__image" src={pf1} />
      </Link>
    </div>
  </div>,
];
export default function Projects() {
  return (
    <>
      <div id="projects" className="projects__title">
        <div className="projects__title-text">Moje projekty</div>
      </div>
      <div className="projects">
        {ol}
        {vg}
        {om}
        {HK}
        {pf}
      </div>
    </>
  );
}
