// parallax stuff
const parallax = () => {
  const parallax = (e) => {
    document.querySelectorAll(".layer").forEach((layer) => {
      const speed = layer.getAttribute("data-speed");
      const x = (window.innerWidth - e.pageX * speed) / 100;
      const y = (window.innerHeight - e.pageY * speed) / 100;
      layer.style.transform = `translateX(${x}px) translateY(${y}px)`;
    });
  };

  const parallaxCondition = () => {
    if (
      /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(
        navigator.userAgent
      )
    ) {
      return;
    } else {
      document.addEventListener("mousemove", parallax);
    }
  };
  parallaxCondition();
};

const cursorSetup = () => {
  const cursor1 = document.querySelector(".cursor1");
  const cursor2 = document.querySelector(".cursor2");

  const cursorMove = (e) => {
    cursor1.style.top = e.pageY - 2 - 5 + "px";
    cursor1.style.left = e.pageX - 2 - 5 + "px";
    cursor2.style.top = e.pageY - 15 - 5 + "px";
    cursor2.style.left = e.pageX - 15 - 5 + "px";
  };

  const cursorExpand = () => {
    cursor1.classList.add("cursor-expand");
    setTimeout(() => {
      cursor1.classList.remove("cursor-expand");
    }, 500);
  };

  document.addEventListener("mousemove", cursorMove);
  document.addEventListener("scroll", cursorMove);
  document.addEventListener("wheel", cursorMove);
  document.addEventListener("click", cursorExpand);
};

export { cursorSetup, parallax };
